 div .progress-page {
     display: flex;
     align-items: end;
     justify-content: center;
     height: 100%;
     overflow: hidden;
     background-color: #fff;
 }

 div .progress-container {
     display: flex;
     flex-direction: column;
     align-items: center;
     background-color: #fff;
     height: 70%;
     justify-content: space-between;
 }

 div .progress-content {
     display: flex;
     flex-direction: column;
     gap: 58px;
 }