about logo {
    display: flex;
    transform: translate3d(0, 0, 0);
    justify-content: center;
    padding-top: calc(env(safe-area-inset-top) + clamp(16px, 5vw, 32px));
    padding-bottom: clamp(16px, 5vw, 32px);
    position: fixed;
    width: 100%;
    z-index: 3;
    transform: translate3d(0, 0, 0);
    will-change: transform;

}
about logo img {
        filter: drop-shadow(0px 0px 12px rgba(0, 0, 0, 0.18));
        transition: filter 0.2s ease-in-out;
        will-change: filter;
}
/* about logo.noshadow {
    background-color: #007AFF;
} */
about logo.noshadow img {
    filter: drop-shadow(0px 0px 12px rgba(0, 0, 0, 0));

}

about .logomark {
    width: clamp(35px, 10vw, 50px);
}

about .logotext {
    width: clamp(115px, 30vw, 130px);
}

logobackground {
    position: fixed;
    backdrop-filter: blur(0.5px);
    height: 10vh;
    min-height: 90px;
    width: 100%;
    z-index: 3;
    mask: linear-gradient(0deg, #FFFFFF00 0%, #010101 75%);
    -webkit-mask: linear-gradient(0deg, #FFFFFF00 0%, #010101 75%);
    transform: translate3d(0, 0, 0);

}

about .progressLogo {
    width: "100px";
}

about .whiteIcon {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}