about {
    height: 100vh;
    height: 100svh;
    overflow-y: scroll;
    overflow-x: hidden;
    scroll-behavior: smooth;
    background-color: #007AFF;
    scroll-snap-type: y mandatory;
}
firstpage {
    position: relative;
    display: block;
}
about {
    font-size: clamp(90%, 4vw, 102%);
}

about h1, about p {
    margin: 0;
    text-wrap: balance;
    text-align: center;
}
about h1 {
    color: #344767;
    font-weight: 600;
}
about p {
    color: #7B809A;
    font-weight: 400;
    font-size: clamp(95%, 4vw, 110%);
}

about texts {
    display: flex;
    flex-direction: column;
    gap: 3vh;
    gap: 3svh;
    justify-content: center;
    padding: clamp(16px,5vw, 32px);
    z-index: 1;
}