.sing-up-page {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 50px;
    padding-top: 40px;
    padding-bottom: 30px;
    flex-direction: column;
    background-color: #fff !important;
    /* padding-top: 300px; */
}

.sing-up-page .install-logo {
    width: 100%;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
}



.title-container {
    background-color: #fff;
    display: flex;
    flex-direction: column;
    gap: "30px";
    padding-bottom: 30px;
    width: 100%;
}

.title-container h1 {
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 600;
    font-size: 30px;
    line-height: 35px;
    font-family: "Roboto";
    font-size: 24px;
    color: #344767;
    ;
}

.title-container p {
    font-family: Roboto;
    font-size: 16px;
    font-weight: 400;
    line-height: 19px;
    letter-spacing: 0em;
    text-align: center;
    color: #7B809A;

}
