about .card {
    display: block;
    height: 53vh;
    height: 53svh;
    margin-top: -3vh;
    border-radius: 3vh;
    background-color: #fefefe;
    display: flex;
    flex-direction: column;
    z-index: 1;
    position: relative;
    filter: drop-shadow(0px 0px 24px #00000043);
    transform: translate3d(0, 0, 0);
    overflow: scroll;
}

about .card texts {
    padding-bottom: 0;
    padding-top: 0;
}

about .spacer {
    flex-grow: 1;
    opacity: 0;
}