.enable-notifications {
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    overflow: visible;

}

.enable-notifications .enable-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: space-between;
}

.enable-notifications .enable-container .enable-head {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    gap: 55px;
    padding-bottom: 55px;
    text-align: center;
}

.enable-notifications .enable-container .enable-head .head-text h1 {
    color: #344767;
}

.enable-notifications .enable-container .enable-head .head-text p {
    color: #7B809A;
}

.enable-notifications .enable-container .enable-head logocolor {
    padding-top: 25px;
    padding-bottom: 10px;
}

.enable-notifications .enable-container .enable-body {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    gap: 45px;
    width: 100%;
    height: 100%;
}

.enable-notifications .enable-container .enable-body a {
    text-decoration: none;
    display: flex;
    align-items: center;
    color: #2d87eb;
}