
.installpage {
    display: flex;
    background-color: #fefefe;
    align-items: center;
    flex-direction: column;
    height: 100%;
    gap: 2vh;
}
.installpage  logo {
    background-color: #fefefe;
    gap: 10px;
}
.installpage  logo img {
    filter: none;
}
useraction {
    display: flex;
    flex-direction: column;
    gap: 2vh;
}

install {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    gap: 2vh;
    height: 100%;
    margin: clamp(78px, 10vh, 100px) 0;
    margin-bottom: 0;
}
.installpage a {
    flex-grow: 0;
    margin-top: 0;
}

.install-title-container {
    padding-bottom: 99px;
}
.footer {
    position: relative;
    bottom: 0;
    left: 0;
    width: 100%;
}
