.login-account {
    display: block;
    height: 53vh;
    height: 53svh;
    margin-top: -3vh;
    border-radius: 3vh;
    background-color: #fefefe;
    display: flex;
    flex-direction: column;
    z-index: 1;
    position: relative;
    filter: drop-shadow(0px 0px 24px #00000043);
    transform: translate3d(0, 0, 0);
}

.login-account {
    padding-bottom: 0;
    padding-top: 0;
}

.login-account textslogin h1 {
    font-family: Roboto;
    font-size: 30px;
    font-weight: 600;
    line-height: 35px;
    letter-spacing: 0em;
    text-align: center;
    padding-top: 32px;
}

.login-account .spacer {
    flex-grow: 1;
    opacity: 0;
}

.login-account a {
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #007AFF !important;
    background-color: #fff;
    flex-grow: 1;
    margin-top: clamp(23px, 3.5vh, 50px);
    margin-top: clamp(23px, 3.5svh, 50px);
    font-size: 100%;
}