.opacity-landing-page {
    background-color: #fff;
}

.opacity-landing-page .opacity-container {
    overflow: hidden;
    background-color: #fff;
    margin: 0;
    padding: 0;
}

.opacity-landing-page opacitypic {
    scroll-snap-align: start;
    height: clamp(250px, 100vh, 750px);
    height: clamp(250px, 100svh, 750px);

    display: flex;
    flex-direction: column;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    justify-content: center;
    position: relative;
    filter: blur(4px);
    opacity: 0.98;
}

div opacitypage .opacity-container {
    padding: 0;
    margin: 0;
}

div opacitypage opacitylogo {
    padding-bottom: 34px;
    position: absolute;
    z-index: 999;
    width: 100%;
    top: 297px;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 1;
    filter: blur(0px);
}