about footer {
    scroll-snap-align: start;
    color: #fefefe;
    height: calc(100vh);
    height: calc(100svh);
    position: relative;
    z-index: 2;
    display: flex;
    justify-content: center;
    /* align-items: center; */
}
.signup {
    padding: clamp(16px, 5vw, 32px);
    padding-bottom: 0;
    padding-top: 0;
    flex-direction: column;
    gap: 1.44rem;
    display: flex;
    margin-top: clamp(70px, 10vh, 100px);
    max-width: 500px;
}

about input {
    -webkit-text-fill-color: #fefefe !important;
    padding: 11px !important;
}

about .MuiInputLabel-shrink {
    color: #fefefe !important;
}

footer div {
    display: flex;
    justify-content: center;
}

footer p,
footer h1 {
    text-align: center;
    color: #fefefe;
}

footer p {
    padding-bottom: 2rem;
}

about fieldset {
    border: 1px solid;
    border-color: rgba(52, 71, 103, 0.25) !important;
    border-radius: 0.625rem !important;
}

.Mui-focused .MuiOutlinedInput-notchedOutline {
    border-width: 1px !important;
}

about .MuiTextField-root:has(input:focus) fieldset {
    border-color: #fefefe !important;
}

about .MuiFormControlLabel-root {
    margin: 0 !important;
}

about .MuiFormControlLabel-root span {
    padding: 0;
    margin-left: 0 !important;
}

.MuiFormControlLabel-label {
    transform: none !important;
}

.textButton {
    color: #328cec !important;
    background-color: #fff !important;
    opacity: 1 !important;
}

.checkbox-landing-page {
    border-radius: 50% !important;
}

.signup .MuiTextField-root label {
    color: #83b7f1;
}

.signup .MuiTextField-root.focused label {
    color: #fff;
}

.signup .MuiTextField-root.filled label {
    color: #fff;
}

.custom-checkbox .MuiCheckbox-root {
    border-radius: 50%;
}