
about button {
    border-radius: 4.375rem;
    background: #007AFF;
    color: #fefefe;
    border: none;
    padding: 0.625rem;
    font-size: 120%;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    margin-top: calc(clamp(calc(300px - 3vh), 50vh, calc(600px - 3vh)) - clamp(25px, 3.5vh, 50px));
    margin-top: calc(clamp(calc(300px - 3vh), 50svh, calc(600px - 3vh)) - clamp(25px, 3.5svh, 50px));
    width: clamp(100px, 50vw, 300px);
    height: clamp(50px, 7vh, 100px);
    height: clamp(50px, 7svh, 100px);
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
    z-index: 2;
}